/* Sidebar.css */
.sidebar {
    width: 250px; /* Fixed width for the sidebar */
    height: 100vh; /* Ensures the sidebar spans the full height of the viewport */
    background: linear-gradient(135deg, #1c1e2b, #4a3d2c); /* Gradient background */
    color: #ffffff; /* White text */
    padding: 20px;
    box-shadow: 2px 0 10px rgba(0, 0, 0, 0.3); /* Subtle shadow for separation */
    overflow-y: auto; /* Scrollable content if necessary */
    display: flex;
    flex-direction: column; /* Stack content vertically */
    gap: 20px; /* Space between sections */
    transition: all 0.3s ease; /* Smooth transitions for hover effects */
}

/* Sidebar Animation */
.sidebar:hover {
    width: 270px; /* Slightly expand the sidebar on hover */
    box-shadow: 4px 0 15px rgba(0, 0, 0, 0.4); /* Enhance shadow */
}

/* List styles */
.sidebar ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    gap: 20px; /* Increased space between items */
}

/* List items */
.sidebar ul li {
    margin: 0; /* Remove default margin */
}

/* Links */
.sidebar ul li a {
    color: #ffb84d; /* Amber for links */
    text-decoration: none;
    font-size: 1.2rem; /* Slightly larger text */
    font-weight: bold;
    letter-spacing: 0.5px; /* Slightly wider letters for readability */
    padding: 10px 15px; /* Add padding for larger clickable area */
    border-radius: 5px; /* Rounded corners */
    transition: all 0.3s ease; /* Smooth animation */
}

/* Hover effects */
.sidebar ul li a:hover {
    color: #e69a2e; /* Darker amber on hover */
    background: rgba(255, 184, 77, 0.2); /* Subtle background highlight */
    transform: translateX(10px); /* More pronounced slide effect on hover */
    box-shadow: inset 5px 0 0 rgba(255, 184, 77, 0.5); /* Left border highlight */
}

/* Active link styling (optional, based on a class) */
.sidebar ul li a.active {
    background: rgba(255, 184, 77, 0.4); /* Brighter background */
    color: #ffffff; /* White text for active link */
    box-shadow: inset 5px 0 0 #ffb84d; /* Bold left border for active link */
}

/* Sidebar heading */
.sidebar h2 {
    font-size: 1.5rem;
    font-weight: bold;
    margin-bottom: 20px;
    text-align: center;
    letter-spacing: 1px;
    color: #ffb84d;
}

/* Section separation */
.sidebar hr {
    border: none;
    border-top: 1px solid rgba(255, 255, 255, 0.2);
    margin: 20px 0;
}


/* Logo container */
.logo-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px; /* Space below the logo */
}

/* Logo image */
.logo {
    max-width: 80%;
    height: auto; /* Maintain aspect ratio */
    filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2)); /* Subtle shadow for pop */
    transition: transform 0.3s ease;
}

.logo:hover {
    transform: scale(1.1); /* Slight zoom on hover */
}
