/* ExportPage.css */
.export-page {
    font-family: Arial, sans-serif;
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
}

h1 {
    text-align: center;
    margin-bottom: 20px;
    color: #4a4a4a;
}

.filters-section {
    display: flex;
    flex-wrap: wrap;
    gap: 15px;
    margin-bottom: 20px;
}

.filters-section label {
    flex: 1 1 200px;
    display: flex;
    flex-direction: column;
    font-size: 14px;
    color: #555;
}

.filters-section input,
.filters-section select {
    margin-top: 5px;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.documents-table {
    margin-top: 20px;
    border: 1px solid #ccc;
    border-radius: 4px;
    overflow: hidden;
}

.documents-table table {
    width: 100%;
    border-collapse: collapse;
}

.documents-table th,
.documents-table td {
    text-align: left;
    padding: 10px;
    border-bottom: 1px solid #ccc;
}

.documents-table th {
    background-color: #f9f9f9;
    font-weight: bold;
}

.documents-table tr:hover {
    background-color: #f1f1f1;
}

.export-options {
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.export-options label {
    font-size: 14px;
    color: #555;
}

.export-options select {
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

button {
    background-color: #4caf50;
    color: white;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease;
}

button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
}

button:hover:not(:disabled) {
    background-color: #45a049;
}

.export-message {
    margin-top: 20px;
    text-align: center;
    font-size: 16px;
    color: #4caf50;
}

.error-message {
    color: red;
    font-weight: bold;
    text-align: center;
}
