/* src/pages/UploadPage.css */

/* Page Container */
.upload-page {
    padding: 40px 20px;
    max-width: 800px;
    margin: 0 auto;
    background: linear-gradient(135deg, #1c1e2b, #2b2e4a);
    border-radius: 10px;
    color: #ffffff;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.3);
    font-family: 'Arial', sans-serif;
}

/* Page Heading */
.upload-page h1 {
    text-align: center;
    margin-bottom: 30px;
    color: #4a90e2; /* Vibrant blue for heading */
}

/* Upload Method Section */
.upload-method {
    display: flex;
    justify-content: center;
    gap: 20px;
    margin-bottom: 30px;
}

.upload-method label {
    display: flex;
    align-items: center;
    cursor: pointer;
    color: #ffffff;
}

.upload-method input[type="radio"] {
    margin-right: 10px;
    accent-color: #4a90e2; /* Match the app's primary color */
}

/* File Input Section */
.file-preview {
    margin-top: 20px;
    padding: 10px;
    background: #2d2f41; /* Dark card background */
    border-radius: 8px;
    box-shadow: 0px 5px 15px rgba(0, 0, 0, 0.2);
}

.file-preview h3 {
    margin-bottom: 10px;
    font-size: 1.2rem;
    color: #50e3c2; /* Light teal for subheadings */
}

.file-preview ul {
    list-style-type: none;
    padding: 0;
}

.file-preview li {
    padding: 5px;
    border-bottom: 1px solid #444;
    color: #ffffff;
}

.file-preview li:last-child {
    border-bottom: none;
}

/* Upload Button */
button {
    display: block;
    margin: 20px auto 0;
    background: #4a90e2;
    color: #ffffff;
    border: none;
    padding: 12px 20px;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.3s ease;
}

button:disabled {
    background: #555;
    cursor: not-allowed;
}

button:hover:not(:disabled) {
    background: #50e3c2;
    transform: translateY(-2px);
}

/* Upload Progress Bar */
.upload-progress {
    margin-top: 20px;
    text-align: center;
}

.upload-progress progress {
    width: 100%;
    height: 10px;
    border-radius: 5px;
    overflow: hidden;
    appearance: none;
}

.upload-progress progress::-webkit-progress-bar {
    background: #2d2f41;
    border-radius: 5px;
}

.upload-progress progress::-webkit-progress-value {
    background: #4a90e2;
    border-radius: 5px;
}

.upload-progress span {
    display: block;
    margin-top: 10px;
    color: #ffffff;
    font-size: 1rem;
}

/* Drag-and-Drop Area */
.drag-drop-area {
    border: 2px dashed #4a90e2; /* Match your app's theme */
    padding: 20px;
    text-align: center;
    border-radius: 8px;
    background-color: #2d2f41; /* Dark background */
    color: #ffffff;
    transition: background-color 0.3s ease;
    cursor: pointer;
    margin-bottom: 20px;
}

.drag-drop-area {
    border: 2px dashed #ccc;
    padding: 20px;
    text-align: center;
    cursor: pointer;
    transition: border-color 0.2s ease;
}

.drag-drop-area:hover {
    border-color: #aaa;
}

.drag-drop-area.active {
    border-color: #007bff;
}
