.settings-page {
    padding: 20px;
    font-family: Arial, sans-serif;
}

.tabs {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

.tabs button {
    padding: 10px 20px;
    border: none;
    background-color: #f4f4f4;
    cursor: pointer;
}

.tabs button.active {
    background-color: #6200ea;
    color: white;
}

.tab-content {
    margin-top: 20px;
}

label {
    display: block;
    margin-bottom: 10px;
}

label input {
    margin-top: 5px;
    padding: 5px;
    width: 100%;
}

button {
    padding: 10px 15px;
    margin-top: 10px;
    background-color: #6200ea;
    color: white;
    border: none;
    cursor: pointer;
}

button:hover {
    background-color: #4500c4;
}

.sign-out-section {
    margin-top: 40px;
}

.sign-out-button {
    padding: 10px 20px;
    background-color: #e53935;
    color: white;
    border: none;
    cursor: pointer;
}

.sign-out-button:hover {
    background-color: #b71c1c;
}

.save-button {
    margin-top: 10px;
    padding: 10px 15px;
    background-color: #4caf50;
    color: white;
    border: none;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.3s ease;
}

.save-button:hover {
    background-color: #388e3c;
}

.save-button:disabled {
    background-color: #9e9e9e;
    cursor: not-allowed;
}
