/* Main Container for Login Page */
.login-container {
    display: flex;
    height: 100vh;
    background: linear-gradient(135deg, #2b2e4a, #1c1e2b); /* Dark gradient for background */
    color: white;
    font-family: 'Arial', sans-serif;
    align-items: center;
    justify-content: center;
}

/* Login Form */
.login-container form {
    background: #2d2f41; /* Dark card background for form */
    border-radius: 15px;
    padding: 30px;
    width: 100%;
    max-width: 400px;
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.3); /* Add depth with shadow */
    text-align: center;
}

/* Form Inputs */
.login-container input {
    width: 100%;
    padding: 12px;
    margin-bottom: 20px;
    background: #1f2130;
    border: none;
    border-radius: 10px;
    font-size: 1rem;
    color: white;
}

.login-container input::placeholder {
    color: #8a8a8a; /* Muted placeholder text color */
}

.login-container input:focus {
    outline: none;
    border: 2px solid #4a90e2; /* Blue border on focus */
}

/* Submit Button */
.login-container button {
    width: 100%;
    background: #4a90e2; /* Vibrant blue button */
    color: white;
    border: none;
    padding: 12px;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: background 0.3s ease;
}

.login-container button:hover {
    background: #50e3c2; /* Teal hover effect */
}

/* Error Message */
.login-container .error-message {
    margin-top: 10px;
    color: #ff6b6b; /* Vibrant red for errors */
    font-size: 0.9rem;
}

/* Logo on the login page */
.login-logo {
    display: block;
    margin: 0 auto 20px; /* Center and add spacing below */
    max-width: 150px; /* Scale the logo size */
    height: auto; /* Maintain aspect ratio */
}
