@import url('https://fonts.googleapis.com/css2?family=Arimo:ital,wght@0,400..700;1,400..700&display=swap');

.navbar-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem 2rem; /* Smaller padding for consistent navbar size */
    background-color: white;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: sticky;
    top: 0;
    z-index: 1000;
    height: 80px; /* Fixed height for navbar */
}

.navbar-logo {
    display: flex;
    align-items: center;
}

.navbar-logo .logo-image {
    height: 200px; /* Keep the large size for the logo */
    max-height: none; /* Prevent the logo from scaling down */
    width: auto;
    object-fit: contain;
    position: absolute; /* Allows it to float outside the navbar bounds */
    top: 50%; /* Vertically align */
    transform: translateY(-50%);
}

.navbar-nav {
    display: flex;
    align-items: center;
}

.nav-links {
    list-style: none;
    display: flex;
    gap: 1.5rem;
}

.nav-links a {
    text-decoration: none;
    color: #333;
    font-weight: bold;
    cursor: pointer;
}

.nav-links a:hover {
    color: #ffbf00;
}

.nav-actions {
    display: flex;
    gap: 1rem;
}

.login-button,
.signup-button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-weight: bold;
}

.login-button {
    background: transparent;
    color: #333;
}

.signup-button {
    background: #ffbf00;
    color: white;
}

.hamburger {
    background: none;
    border: none;
    font-size: 1.5rem;
    cursor: pointer;
    display: none;
}

/* Responsive Navbar */
@media (max-width: 768px) {
    .hamburger {
        display: block;
    }

    .navbar-nav {
        flex-direction: column;
        position: absolute;
        top: 60px;
        right: 0;
        background: white;
        width: 100%;
        display: none;
    }

    .navbar-nav.nav-open {
        display: flex;
    }

    .nav-links {
        flex-direction: column;
        gap: 1rem;
    }
}
