/* Page Container */
.register-page {
    display: flex;
    height: 100vh; /* Full viewport height */
    background: linear-gradient(135deg, #1c1e2b, #4a3d2c); /* Dark gradient to muted amber */
    color: white;
    font-family: 'Arial', sans-serif;
}

/* Left Section (Image or Slogan) */
.register-page .left-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center; /* Center content vertically */
    align-items: center; /* Center content horizontally */
    padding: 20px;
    background: url('https://source.unsplash.com/random/800x600') center/cover no-repeat; /* Replace with your image */
    color: white;
    box-shadow: inset -5px 0 10px rgba(0, 0, 0, 0.5); /* Subtle shadow for separation */
}

.register-page .left-section h2 {
    font-size: 2.5rem;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
}

.register-page .left-section p {
    font-size: 1.2rem;
    text-align: center;
    line-height: 1.5;
}

/* Right Section (Form) */
.register-page .right-section {
    flex: 1;
    display: flex;
    justify-content: center; /* Center horizontally */
    align-items: center; /* Center vertically */
    padding: 40px 20px;
    background: #2d2f41; /* Darker card-like background */
    box-shadow: inset 5px 0 10px rgba(0, 0, 0, 0.5); /* Subtle shadow for separation */
}

/* Form Styling */
.register-page form {
    background: #1f2130; /* Slightly darker card background */
    border-radius: 15px;
    padding: 30px;
    width: 100%;
    max-width: 400px; /* Limit form width */
    box-shadow: 0px 8px 20px rgba(0, 0, 0, 0.3); /* Slight shadow for depth */
}

.register-page form h2 {
    font-size: 1.8rem;
    margin-bottom: 20px;
    text-align: center;
    color: #ffb84d; /* Amber accent for the heading */
}

/* Form Inputs */
.register-page input,
.register-page select,
.register-page textarea {
    width: 100%;
    padding: 12px;
    margin-bottom: 20px;
    background: #2d2f41; /* Input background matching card */
    border: none;
    border-radius: 10px;
    font-size: 1rem;
    color: white;
}

.register-page input::placeholder,
.register-page select,
.register-page textarea::placeholder {
    color: #8a8a8a; /* Muted text for placeholders */
}

.register-page input:focus,
.register-page select:focus,
.register-page textarea:focus {
    outline: none;
    border: 2px solid #ffb84d; /* Amber border for focus */
}

/* Checkbox */
.register-page label {
    display: flex;
    align-items: center;
    font-size: 0.9rem;
    color: #ffffff;
    margin-bottom: 20px;
}

.register-page label input {
    margin-right: 10px;
}

/* Submit Button */
.register-page button {
    width: 100%;
    background: #ffb84d; /* Amber button */
    color: #1c1e2b; /* Dark text for contrast */
    border: none;
    padding: 12px;
    font-size: 1rem;
    font-weight: bold;
    border-radius: 10px;
    cursor: pointer;
    transition: background 0.3s ease, box-shadow 0.3s ease;
    box-shadow: 0 3px 10px rgba(255, 184, 77, 0.3); /* Subtle amber shadow */
}

.register-page button:hover {
    background: #e69a2e; /* Darker amber for hover */
    color: #ffffff; /* White text on hover */
    box-shadow: 0 5px 15px rgba(230, 154, 46, 0.5); /* Stronger shadow */
}

/* Link Styles */
.register-page .link {
    text-align: center;
    font-size: 0.9rem;
    color: #d4c2a8; /* Muted amber for link text */
    margin-top: 10px;
}

.register-page .link a {
    color: #ffb84d; /* Amber for link text */
    text-decoration: none;
}

.register-page .link a:hover {
    text-decoration: underline;
    color: #e69a2e; /* Darker amber on hover */
}
