.search-page {
    padding: 20px;
    font-family: Arial, sans-serif;
}

.search-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.stats {
    display: flex;
    gap: 20px;
}

.stat-item h3 {
    margin: 0;
    font-size: 16px;
    color: #666;
}

.stat-item p {
    margin: 0;
    font-size: 24px;
    font-weight: bold;
}

.filters-section {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    margin-bottom: 20px;
}

.filter-group {
    flex: 1;
    min-width: 200px;
}

.filter-group label {
    display: block;
    font-size: 14px;
    margin-bottom: 5px;
}

.filter-group input,
.filter-group select {
    width: 100%;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.filter-buttons {
    display: flex;
    gap: 10px;
}

.results-table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 20px;
}

.results-table th,
.results-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
}

.results-table th {
    background-color: #f4f4f4;
}

.error-message {
    color: red;
}
