/* AnalyticsReportsPage.css */

/* General Page Layout */
.analytics-reports-page {
    padding: 20px;
    font-family: Arial, sans-serif;
    background-color: #f8f9fa;
    color: #333;
}

.analytics-reports-page h1 {
    text-align: center;
    font-size: 2rem;
    margin-bottom: 20px;
    color: #4a4a4a;
}

/* Chart Containers */
.chart-container {
    margin: 20px auto;
    padding: 20px;
    max-width: 800px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.chart-container h2 {
    font-size: 1.5rem;
    margin-bottom: 15px;
    text-align: center;
    color: #333;
}

/* Chart Wrapper */
.chart-wrapper {
    position: relative;
    width: 100%;
    height: 400px;
}

/* Error Message */
.error-message {
    text-align: center;
    color: red;
    font-weight: bold;
}

/* Loading Message */
.loading-message {
    text-align: center;
    font-size: 1.2rem;
    color: #666;
}

/* Media Queries */
@media (max-width: 768px) {
    .chart-container {
        padding: 15px;
        margin: 10px auto;
    }

    .chart-container h2 {
        font-size: 1.25rem;
    }
}
